.App {
  font-family: sans-serif;
  padding: 2rem;
  background-color: #ffffff;
  color: #333333;
  max-width: 1000px;
  margin: 0 auto;
}
.hero {
  background-color: #f3f3f3;
  padding: 2rem;
  border-radius: 1rem;
  text-align: center;
  margin-bottom: 2rem;
}
.hero h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
.hero p {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}
.buttons button {
  margin: 0 0.5rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 1rem;
  cursor: pointer;
  font-weight: bold;
}
.buttons button:first-child {
  background-color: #000;
  color: #fff;
}
.buttons button:last-child {
  background-color: #fff;
  color: #000;
  border: 1px solid #000;
}
section {
  margin-bottom: 2rem;
}
h2 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
ul li {
  margin-bottom: 0.5rem;
}

/* Centrage et taille responsive du logo */
.logo-container {
  display: flex;
  justify-content: center;
  margin-bottom: 0.1rem;
}

.logo {
  width: 120px;
  max-width: 40vw;
  height: auto;
  opacity: 0.95;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.logo:hover {
  transform: scale(1.05);
  opacity: 1;
}
